import { Check } from '@mui/icons-material';
import './pb.css';


function ProgressComp(props) {
    return (
        <>
            <div className="ds-box pb">
                <div className={props.active === true ? "ds-box-img db-number active-dimb" : "ds-box-img db-number"}>{props.active === true ? <Check/>  : props.number }</div>
                <div>
                    <div className="ds-heading">{props.title}</div>
                </div>
            </div>
            {props.nt ?
                <></>
                :
                <div className={props.active === true ? "connector acive-connector" : "connector"}></div>
            }
        </>
    );
}

export default function Progressbar(props) {
    return (
        <div className="delivery-status pb-c">
            <ProgressComp number={1} active={props.active1} title='Basic Info' />
            <ProgressComp number={2} active={props.active2} title='Request' />
            <ProgressComp number={3} active={props.active3} title='Selection' />
            <ProgressComp number={4} active={props.active4} title='form' nt />
        </div>
    );
};
