import Product from "./Product/Product";

function MappingProducts(props) {

    const val = props.arr;

    const ret = val.map( val => (
        <Product img={val.img} name={val.name} price={val.price} sup={val.sup} loc={val.loc} />
    )) ;

    return (
        ret       
    );
}

export default MappingProducts ;