import './landing.css';
import './sidebar.css';
import Navbar from "./Navbar";
import Brands from "./Brands";
import Quality from "./Quality";
import Work from "./Work";
import { Reviews } from "./Reviews";
import Contact from "./Contact";
import Subscribe from "./Subscribe";
import Footer from "./Footer";

// Landing Images

import Limg1 from '../../assets/indeximg.png'
import Ellipse1 from "../../assets/Ellipse8.png";
import Ellipse2 from "../../assets/Ellipse9.png";
import PopupLogin from '../dashboard/Login/PopupLogin';
import CloseIcon from '@mui/icons-material/Close';

import { useState } from 'react'

function Landing() {

    const [Val, setVal] = useState(false)



    return (
        <>
            {/* NAVBAR + SECTION-1 */}
            <section className="landing-sec-1">

                <Navbar />

                <div className="sec1-div">
                    <h1>Reduce Cost <br /> Increase Profits.</h1>
                    <p>Join the RasayanKart trade network to <br /> Buy and/or Sell cchemicals online.</p>
                    <button onClick={() => setVal(true)}>Join Our Network</button>
                </div>

                <div className="LandingImg">
                    <img className="li-1" src={Ellipse2} alt=" " />
                    <img className="li-2" src={Ellipse1} alt=" " />
                    <img className="li-3" src={Limg1} alt=" " />
                </div>


                <div className={Val === true ? "popup-c active-popup" : "popup-c"}>
                    <div className="login-signup-overlay"></div>
                    <div style={{ height: '300px' }} className="login-signup-box">
                        <div className="sub-popup landing-sec-7">
                            <CloseIcon className='popup-i' onClick={() => setVal(false)} />
                            <h2>Subscribe to our news</h2>
                            
                            <p style={{marginBottom:'0px'}}>Lorem ipsum dolor sit amet consectetur adipisicing elit. Unde quis necessitatibus cum!</p>
                            
                            <form style={{marginTop:'29px'}} action='' className="">
                                <input style={{width:'139px'}} type="email" name='sub-email' placeholder='example123@gmail.com' />
                                <button style={{width:'66px'}} type='submit'>Send</button>
                            </form>

                        </div>
                    </div>
                </div>



            </section>


            <Brands />


            <Quality />


            <Work />


            <Reviews />


            <Contact />


            <Subscribe />


            <Footer />
        </>
    );
}

export default Landing;