import "./navbar.css";
import "./sidebar.css";
import Vs from "../../../../assets/vs.png";
import Rklogo from "../../../../assets/rklogo.png";
import SearchIcon from "@mui/icons-material/Search";
import PersonIcon from "@mui/icons-material/Person";
import KeyboardArrowDownIcon from "@mui/icons-material/KeyboardArrowDown";
import ShoppingCartOutlinedIcon from "@mui/icons-material/ShoppingCartOutlined";
import * as React from "react";
import Menu from "@mui/material/Menu";
import MenuIcon from "@mui/icons-material/Menu";
import Backdrop from "@mui/material/Backdrop";
import Bag from "../../../../assets/Bag_fill.png";
import { BrowserRouter as Router, Link } from "react-router-dom";
import { useState } from "react";
import CloseIcon from "@mui/icons-material/Close";


import { styled } from '@mui/material/styles';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';

import StoreIcon from '@mui/icons-material/Store';
import LocalMallIcon from '@mui/icons-material/LocalMall';
import InfoIcon from '@mui/icons-material/Info';
import LightbulbIcon from '@mui/icons-material/Lightbulb';
import EmailIcon from '@mui/icons-material/Email';
import LogoutIcon from '@mui/icons-material/Logout';
import ExitToAppIcon from '@mui/icons-material/ExitToApp';
import { Email, InsertDriveFile, TextSnippet, Wallet } from "@mui/icons-material";
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth';


function Button(props) {
    return (
        <Link
            to={props.to}
            className={
                props.val === true ? "sidebar-spl-btn active-btn" : "sidebar-spl-btn"
            }
        >
            {props.logo}
            {props.title}
        </Link>
    );
}




function Navbar() {
    const [Sidebar, setSidebarActive] = useState(false);

    const SidebarFunc = () => {
        Sidebar ? setSidebarActive(false) : setSidebarActive(true);
    };

    const [anchorEl, setAnchorEl] = React.useState(null);
    const [open1, setOpen] = React.useState(false);
    const open = Boolean(anchorEl);
    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
        setOpen(!open1);
    };
    const handleClose = () => {
        setAnchorEl(null);
        setOpen(false);
    };



    const IOSSwitch = styled((props) => (
        <Switch focusVisibleClassName=".Mui-focusVisible" disableRipple {...props} />
    ))(({ theme }) => ({
        width: 42,
        height: 26,
        padding: 0,
        '& .MuiSwitch-switchBase': {
            padding: 0,
            margin: 2,
            transitionDuration: '300ms',
            '&.Mui-checked': {
                transform: 'translateX(16px)',
                color: '#fff',
                '& + .MuiSwitch-track': {
                    backgroundColor: theme.palette.mode === 'dark' ? '#2ECA45' : '#65C466',
                    opacity: 1,
                    border: 0,
                },
                '&.Mui-disabled + .MuiSwitch-track': {
                    opacity: 0.5,
                },
            },
            '&.Mui-focusVisible .MuiSwitch-thumb': {
                color: '#33cf4d',
                border: '6px solid #fff',
            },
            '&.Mui-disabled .MuiSwitch-thumb': {
                color:
                    theme.palette.mode === 'light'
                        ? theme.palette.grey[100]
                        : theme.palette.grey[600],
            },
            '&.Mui-disabled + .MuiSwitch-track': {
                opacity: theme.palette.mode === 'light' ? 0.7 : 0.3,
            },
        },
        '& .MuiSwitch-thumb': {
            boxSizing: 'border-box',
            width: 22,
            height: 22,
        },
        '& .MuiSwitch-track': {
            borderRadius: 26 / 2,
            backgroundColor: theme.palette.mode === 'light' ? '#E9E9EA' : '#39393D',
            opacity: 1,
            transition: theme.transitions.create(['background-color'], {
                duration: 500,
            }),
        },
    }));


    const shoot = () => {
        window.location.href = "https://prismatic-praline-c809b0.netlify.app/#/"
    }




    return (





        <nav className="dashboard-nav">
            <div className="df aic">
                <div onClick={SidebarFunc} className="nav-slider-btn">
                    <MenuIcon className="slider-btn" />
                </div>
                <Link to="/" className="nav-brand">
                    <img src={Rklogo} className="rklogo" alt="" />
                    <span>Rasayan</span>Kart
                </Link>
            </div>

            <div className="nav-searchbox">
                <input placeholder="Search Product By name & CAS number" type="text" />
                <button>
                    <SearchIcon />
                </button>
            </div>
            <div className="nav-spans">
                <Link to="/shop">Buy</Link>
                <Link to="/seller">Sell</Link>
            </div>
            <div className="nav-corner">
                <Link to='/cart' ><img src={Bag} className="bag-icon" alt="" /></Link>
                <div className="nav-dropdown">
                    <button className="" id="basic-button">
                        <PersonIcon
                            aria-controls={open ? "basic-menu" : undefined}
                            aria-haspopup="true"
                            aria-expanded={open ? "true" : undefined}
                            onClick={handleClick}
                        />
                        <img className="vs-img" src={Vs} alt="" />
                        <Backdrop
                            sx={{ color: "#fff", zIndex: (theme) => theme.zIndex.drawer + 1 }}
                            open={open1}
                            onClick={handleClose}
                        >
                            <Menu
                                id="basic-menu"
                                className="basic-menu"
                                anchorEl={anchorEl}
                                open={open}
                                onClose={handleClose}
                                MenuListProps={{
                                    "aria-labelledby": "basic-button",
                                }}
                            >
                                <Link to="/rfqbasic" className="nav-list" onClick={handleClose}>
                                    Rfq Pages
                                </Link>
                                <Link to="/cart" className="nav-list" onClick={handleClose}>
                                    Cart
                                </Link>
                                <Link to="/busers" className="nav-list" onClick={handleClose}>
                                    Buyers Profile
                                </Link>
                                <Link to="/" className="nav-list" onClick={handleClose}>
                                    Logout
                                </Link>
                            </Menu>
                        </Backdrop>
                        <KeyboardArrowDownIcon
                            className="basic-menu-dd"
                            aria-controls={open ? "basic-menu" : undefined}
                            aria-haspopup="true"
                            aria-expanded={open ? "true" : undefined}
                            onClick={handleClick}
                        />
                    </button>
                </div>
            </div>








            {/* SIDEBAR */}







            <div
                className={
                    Sidebar === true
                        ? "nav-sidebar df fdc nav-sidebar-active"
                        : "nav-sidebar df fdc "
                }
            >
                <div className="nav-sidebar-header df aic jcb">
                    <Link to="/" className="nav-brand">
                        <img src={Rklogo} className="rklogo-sidebar" alt="" />
                        <span>Rasayan</span>Kart
                    </Link>
                    <div onClick={SidebarFunc} className="nav-slider-btn">
                        <CloseIcon className="slider-btn" />
                    </div>
                </div>

                <div className="nav-sidebar-body">
                    <Button
                        to={"/shop"}
                        title="Buy on rasayan kart"
                        logo={<ShoppingCartOutlinedIcon className="nsb-icon" />}
                        val={1}
                    />
                    <Button
                        to={"/seller"}
                        title="Sell on rasayan kart"
                        logo={<StoreIcon className="nsb-icon" />}
                    />
                    <Button
                        to={"/cart"}
                        title="My Cart"
                        logo={<LocalMallIcon className="nsb-icon" />}
                    />

                    <div className="nsb-heading">Buyer's Profile</div>
                    <div className="line-nsb"></div>

                    <Button
                        to={"/busers"}
                        title="My Account"
                        logo={<StoreIcon className="nsb-icon" />}
                    />
                    <Button
                        to={"/bh"}
                        title="Buyers History"
                        logo={<CalendarMonthIcon className="nsb-icon" />}
                    />
                    <Button
                        to={"/br"}
                        title="My RFQs"
                        logo={<TextSnippet className="nsb-icon" />}
                    />
                    <Button
                        to={"/bw"}
                        title="Wallet"
                        logo={<Wallet className="nsb-icon" />}
                    />
                    <Button
                        to={"/bd"}
                        title="Documents"
                        logo={<InsertDriveFile className="nsb-icon" />}
                    />

                    <div className="line-nsb"></div>

                    <Button
                        to={""}
                        title="Help Center"
                        logo={<InfoIcon className="nsb-icon" />}
                    />
                    <Button
                        to={""}
                        title="Policy"
                        logo={<LightbulbIcon className="nsb-icon" />}
                    />
                    <Button
                        to={""}
                        title="Notifications"
                        logo={<Email className="nsb-icon" />}
                    />
                    <div className="hehe-div">
                        <FormGroup onClick={shoot} className='df'>

                            <FormControlLabel
                                className='hehe-form'

                                control={<IOSSwitch sx={{ m: 1 }} />}
                                label="Switch to Seller"
                            />
                        </FormGroup>
                    </div>
                </div>
            </div>
        </nav>
    );
}







export default Navbar;
