import "./shop.css";
import Navbar from '../Components/Navbar/Navbar';
import Filter from '../Components/Filters/Filter';
import Navfilters from "../Components/Navfilters/Navfilters";
import Product from "../Components/Product/Product";
import ProductImg from '../../../assets/product.png';
import AddIcon from '@mui/icons-material/Add';
import { useState } from "react";
import { SplFilter } from "../Components/SplFilter/SplFilter";
import Clear from "@mui/icons-material/Clear";
import MappingProducts from "../Components/MappingProducts";
import Footer from "../../landing_page/Footer";





function Shop() {

    let Arr = [
        {
            img:ProductImg,
            name:"Calmin Engineers blue paste 20ml",
            price:50,
            sup:"Amit traders",
            loc:"Goa"
        },
        {
            img:ProductImg,
            name:"Calmin Engineers red paste 20ml",
            price:100,
            sup:"Vicky Sellers",
            loc:"UP"
        },
        {
            img:ProductImg,
            name:"Calmin Tubbers  paste 20ml",
            price:200,
            sup:"Rahul Co",
            loc:"Pune"
        },
        {
            img:ProductImg,
            name:"Calmin Engineers blue liquid 20ml",
            price:75,
            sup:"Vishal traders",
            loc:"Delhi"
        }
    ]

    const [Sidebar, setSidebarActive] = useState(false)

    const SidebarFunc = () => {
        Sidebar ? setSidebarActive(false) : setSidebarActive(true);
    }

    return (

        <>

        <section className="main-section">
            <Navbar />
            <div className="divider">
                <div className={Sidebar === true ? "left-part active-sidebar" : "left-part"}>
                    <Clear className="clr-btn" onClick={SidebarFunc} />
                    <Filter />
                    {/* <button onClick={SidebarFunc} className="callbtn2"><AddIcon className="callbtn2-i" /></button> */}
                </div>
                <div className="right-part">
                    <Navfilters />
                    <SplFilter func2={SidebarFunc} />
                    <div className="shop-card-container">
                        {/* <Product img={ProductImg} />
                        <Product img={ProductImg} />
                        <Product img={ProductImg} />
                        <Product img={ProductImg} /> */}
                        <MappingProducts arr={Arr} />
                    </div>
                </div>

            </div>
        </section>

        <Footer />
</>

    );
}

export default Shop;