

// Import Swiper React components

import { Swiper, SwiperSlide } from 'swiper/react';


import { Navigation, Autoplay } from 'swiper';

import 'swiper/css/bundle';

// Import Swiper styles
import 'swiper/css';
import { ReviewsCard } from './Reviews';

function Slider() {
    return (
        <Swiper
            // install Swiper modules
            modules={[Navigation, Autoplay]}
            slidesPerView={3}
            navigation={false}
            autoplay={{
                delay: 2500,
                disableOnInteraction: false,
            }}
            loop={true}
            breakpoints={{
                350: {
                    slidesPerView: 1
                },
                768: {
                    slidesPerView: 2
                },
                1024: {
                    slidesPerView: 3
                }
            }}
        >

            <SwiperSlide><ReviewsCard name='Puneet Shrivastav' date='13/08/22' /></SwiperSlide>
            <SwiperSlide><ReviewsCard name='Shivam Yogi' date='13/09/22' /></SwiperSlide>
            <SwiperSlide><ReviewsCard name='Tushar Alagh' date='03/08/22' /></SwiperSlide>
            <SwiperSlide><ReviewsCard name='Amanjeet' date='03/08/22' /></SwiperSlide>
            <SwiperSlide><ReviewsCard name='Binod' date='03/08/22' /></SwiperSlide>
            <SwiperSlide><ReviewsCard name='Sharad' date='03/08/22' /></SwiperSlide>

    </Swiper>
    );
}

export default Slider;

